* {
    margin-bottom: 0px;
}
body {
    background-color: #23252C;
}
#root {
    background-color: #F5F5F5;
    color: #2B283A;
    width: 317px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 44px;
    margin-bottom: 44px;
    font-family: Inter;
}
.my-name {
    margin-top: 20px;
    margin-bottom: 0px;
}
.job {
    margin-top: 6.19px;
    margin-bottom: 0px;
    color: #F47D27;
    font-style: normal;
}
.website {
    color: #4A4E74;
    font-weight: 400px;
    font-style: normal;
    margin-top: 9px;
    margin-bottom: 0px;
}
.button-email {
    width: 247px;
    height: 35px;
    font-family: Inter;
    margin-top: 17px;
    background: #FFFFFF;
    border: 1px solid #D1D5DB;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    transition: 0.15s;
}
.button-email:hover{
    background: #54535a;
    color: #FFFFFF;
}
.info {
    text-align: center;
}
.about {
    margin-left: 35px;
    margin-right: 35px;
}
.about-info {
    margin-top: 5px;
    color: #4A4E74;
    font-style: normal;
    font-weight: 400;
    font-size: 10.24px;
}
.title-interest {
    margin-top: 27px;
}

.footer {
    height: 64px;
    background-color: #D5D4D8;
    margin-top: 31px;
    display: flex;
}
#ft {
    margin-left: auto;
    margin-right: auto;
    margin-top: 19px;
}
#icons {
    font-size: 20px;
    text-align: center;
    text-decoration: none;
    color: #918E9B;
    margin: 5px 2px;
    width: 25px;
    height: 25px;
    transition: 0.15s;
}
#icons:hover {
    color: #54535a;
}